#rootHelloAsso {
    padding: 0;
    position: relative;
    overflow: hidden;
    border: 1px solid var(--bleu-foncer);
    border-radius: 0.5rem;
    margin-bottom: 3rem;
}
#rootHelloAsso h2 {
    padding: 1rem;
}
/**
* Header
*/
#rootHelloAsso > nav {
    display: flex;
    flex-direction: row;
    margin: 0 0 2rem 0;
}
.link_chemin, .active {
    display: flex;
    padding: 0.5rem 1rem;
    text-decoration: none;
}
.active {
    color: var(--bleu-foncer) !important;
    background-color: var(--orange-clair);
}
.content_bouton_start {
    display: flex;
    justify-content: center;
    margin: 3rem 0 3rem 0;
}
/**
* Formulaire
*/
.close {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0;
    padding: 1.5rem;
    max-height: 200px;
    height: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
.close h3 {
    text-transform: capitalize;
    margin: 0;
    padding: 0;
}
.rond_valider {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 3rem;
    width: 50;
    height: 50;
    border-radius: 50%;
    color: #ffffff;
    background-color: green;
}
.form_inscription {
    position: relative;
    overflow: hidden;
    max-width: 800px;
    margin: auto;
    transition: height 0.3s linear;
}
.adherent_grid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: 2rem;
}
.label_ligne {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: 2rem;
    margin-bottom: 2rem;
}
.label_adresse {
    display: block;
    margin-bottom: 2rem;
}
.label_radio {
    display: flex;
    padding: 0.5rem 0;
    cursor: pointer;
    transition: background-color 0.3s linear;
}
.label_radio:hover {
    background-color: #efefef;
}
.label_radio > input {
    margin-right: 1rem;
}
.content_membre {
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

/**
* Home
*/
.bandeau_haut {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 1rem;
    margin-bottom: 2rem;
    color: #ffffff;
    background-color: var(--bleu-foncer);
}
.bandeau_haut_titre {
    font-family: 'Open Sans', sans-serif;
    font-weight: normal;
    font-size: 1.1rem;
}
.hello_deconnexion {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    color: #ffffff !important;
    border: 1px solid #ffffff;
    border-radius: 1rem;
    background: none;
}
.bandeau_bas {
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    color: var(--bleu-foncer);
    background-color: #f8f8f8;
}
.hello_infos { text-align: center;}
.content_start {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin: 3rem auto;
}
.content_start > div {
    text-align: center;
}
.titre_adhesion {
    font-family: var(--font-condensed);
    font-weight: 600;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: var(--bleu-foncer);
}
.bt_bleu, .bt_bleu_outline, .bt_vert, .bt_supprimer, .bt_modifier, .bt_ajouter{
    display: inline-flex;
    font-family: var(--font-condensed);
    font-weight: 600;
    font-size: 1.1rem;
    align-items: center;
    text-transform: uppercase;
    justify-content: center;
    padding: 0.6rem 2rem;
    min-width: 120px;
    text-decoration: none;
    color: #ffffff !important;
    background-color: var(--bleu-clair-hover);
    border-radius: 999px;
    transition: background-color 0.3s linear;
}

.bt_modifier, .bt_ajouter {
    display: flex;
    color: var(--black-color) !important;
    font-size: 0.9rem;
    background-color: var(--grey-color);
}
.bt_modifier:hover {
    color: #ffffff !important;
    background-color: green;
}
.bt_ajouter:hover {
    color: #ffffff !important;
    background-color: var(--black-color);
}
.bt_vert {
    background-color: green;
}
.bt_vert:hover {
    background-color: rgb(0, 99, 0);
}
.bt_supprimer {
    background-color: var(--grey-color);
}
.bt_supprimer:hover {
    background-color: var(--black-color);
}
.bt_bleu_outline {
    color: var(--bleu-foncer)!important;
    background-color: #ffffff;
    border: 2px solid var(--bleu-foncer);
}
.bt_bleu_outline:hover {
    background-color: var(--grey-light);
}
.bt_bleu:hover {
    background-color: var(--bleu-foncer);
}

/**
* Licences
*/
.ligne_licence {
    display: block;
}
.sous_ligne_licence {
    display: block;
    padding: 0 0 0 1rem;
}
.label_sous_ligne_licence {
    display: flex;
    align-items: center;
}
fieldset {
    margin: 1rem;
    padding: 1rem;
    border: none;
    background: none;
}
legend {
    font-weight: bold;
    color: var(--bleu-foncer);
}
.fieldset_grey {
    background-color: #f8f8f8;
}

/**
* Total
*/
.hello_total {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    margin: 2rem 0;
    background-color: var(--orange-clair);
}
.hello_total > span {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: right;
    padding: 0.35rem 0.5rem;
    font-size: 1rem;
}
.hello_total > span > b {
    display: flex;
    min-width: 90px;
    justify-content: flex-end;
}

/**
* Navigation bottom
*/
.navig_bottom {
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
}
.navig_bottom > * {
    margin: 0;
}

/**
* Famille
*/
.content_ajouter {
    display: flex;
    justify-content: center;
    padding: 1rem 0;
    margin: 1rem 0;
}
.hello_h4 {
    display: block;
    padding: 0.5rem 0;
    font-weight: bold;
    color: var(--bleu-foncer);
}

/**
* Loader
*/
.hello_loader {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: rgba( 255,255,255, 0.85);
}
.hello_loader::before {
    display: block;
    content: "";
    width: 60px;
    height: 60px;
    border: 6px solid var(--bleu-foncer);
    border-bottom-color: transparent;
    border-radius: 50%;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}
.hello_loader > p { color: var(--bleu-foncer); text-align: center; }
.hello_loader > p > img { margin: 1rem auto; }